import React from "react";
import { render } from "react-dom";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { getWeather } from "../action/action";

class SearchBar extends React.Component {
  constructor(state) {
    super(state);
    this.state = {
      inputValue: "",
    };
    this.onInputChange = this.onInputChange.bind(this);
    this.onSubmitClick = this.onSubmitClick.bind(this);
  }
  onInputChange(event) {
    let value = event.target.value;
    this.setState({
      inputValue: value,
    });
  }
  onSubmitClick(e) {
    e.preventDefault();
    this.props.getWeather(this.state.inputValue);
    this.setState({
      inputValue: "",
    });
    this.refs.input.focus();
  }
  render() {
    return (
      <form onSubmit={this.onSubmitClick} className="form">
        <div className="formContainer">
        <h4 className="caption"></h4>
          <input ref="input" className="input" value={this.state.inputValue} onChange={this.onInputChange} type="text" placeholder="beijing" />
          <button></button>
        </div>
      </form >
    );
  }
}
const mapDispatchToProps = dispatch => bindActionCreators({ getWeather }, dispatch);

export default connect(null, mapDispatchToProps)(SearchBar);


